import React, { useState, useEffect, useRef } from "react";
import axios from "../../../axios";
import "./styles.css";
import "froala-editor/js/plugins.pkgd.min.js";
import "froala-editor/css/froala_editor.pkgd.min.css";
import Froala from "react-froala-wysiwyg";
import FroalaToolbarJson from "../../../components/Properties/FroalaToolbarJson";
import "froala-editor/js/languages/ar";

export default function ReactFroala(props) {
  let FroalaToolbarJson1 = FroalaToolbarJson;
  if (window.selectFeatureToBeIncludedInRichText) {
    FroalaToolbarJson1 = window.selectFeatureToBeIncludedInRichText();
  }
  let toolbarOptions = [];
  function getFeatureForRichTextEditor() {
    Object.keys(FroalaToolbarJson1).forEach((key) => {
      if (
        FroalaToolbarJson1[key] === "true" ||
        FroalaToolbarJson1[key] === true
      ) {
        toolbarOptions.push(key);
      }
    });
    return toolbarOptions;
  }

  const froalaEditorConfig = {
    attribution: false,
    language: `${
      window?.depsatkur_configs?.selectedLanguage === "ar" ||
      window?.depsatkur_configs?.selectedLanguage === "ar-SA"
        ? "ar"
        : null
    }`,
    placeholderText: "Your content goes here!",
    toolbarButtons: {
      moreText: {
        buttons: getFeatureForRichTextEditor(),
        id: props.id,
        buttonsVisible: 25,
      },
    },
    toolbarSticky: true,
    pluginsEnabled: [
      "align",
      "colors",
      "entities",
      "fontSize",
      "fontFamily",
      "link",
      "lists",
      "paragraphFormat",
      "paragraphStyle",
      "save",
      "table",
      "wordPaste",
      "lineHeight",
      "charCounter"
    ],    
    key: "YNB3fH3C9B10D6C5C2F3C-8TMIBDIa1NTMNZFFPFZc1d1Ib2a1E1fA4A3G3C3F2B6B3C4D3C1==",
  };
  const [data, setData] = useState();
  const editorRef = useRef(null);

  useEffect(() => {
    if (props) {
      if (editorRef.current) {
      // Access the Froala Editor instance and set the id attribute
      const froalaEditorInstance = editorRef.current.editor;
      if (froalaEditorInstance) {
        const inputElement = froalaEditorInstance.el;
        if (inputElement) {
          inputElement.setAttribute('id', 'my-froala-editor');
        }
      }
    }
      if (window.onLoadRichTextEditor) {
        window.onLoadRichTextEditor(props.data.id);
      }
      let jsonToSend = {
        controlId: props.data.id,
        richTextEditorJSON: [{ id: props.data.id, IndexString: "" }],
        gdtoken: props.gdToken,
        witoken: props.wiToken,
      };

      axios
        .post("/viewform/fetchRichTextEditorData", jsonToSend, {
          withCredentials: true,
        })
        .then((response) => {
          props.data.value = response?.data?.value?.[0]?.value;
          setData(response?.data?.value?.[0]?.value);
        });
    }
  }, []);

  const modelChange = (model) => {
    let edData = model;
    let elem = document.createElement("div");
    elem.innerHTML = edData;
    console.log(elem);
    props.handleContent(model);
    setData(model);
  };

  if (props) {
    return (
      <div
        className="App"
        id={props.id}
        elem_type={props.elem_type}
        style={
          props.previewmode &&
          (props?.data?.style?.["Enable"] === "false" ||
            props?.data?.style?.["Enable"] === false ||
            props?.data?.style?.["ReadOnly"] === "true" ||
            props?.data?.style?.["ReadOnly"] === true)
            ? { pointerEvents: "none" }
            : null
        }
      >
        <Froala
          id={props.id}
          model={data}
          ref={editorRef}
          onModelChange={modelChange}
          tag="textarea"
          config={froalaEditorConfig}
        />
      </div>
    );
  }
}
