import React, { useState, useRef, useEffect } from "react";
import { Form, Row, Col } from "react-bootstrap";
import ElemDeleteControl from "./ElemDeleteControlWrapper";
import styles2 from "../css/COLUMN.module.css";
import { StyleSheet, css } from "aphrodite";
import { CSSPreparation1 } from "../../components/Properties/CSSPreparation1.jsx";
import RichtextEditorUpArrow from "../../Icons/RichtextEditorUpArrow";
import RichtextEditorDownArrow from "../../Icons/RichtextEditorDownArrow";
import Tooltip from "../../ReusableComponents/Tooltip/Tooltip";
import QuickTools from "../../ReusableComponents/QuickTools/QuickTools";
import { getSuggestedId } from "../../components/IFormDesignerLayout/Logic/UserIdLogic";
import MandatoryMsg from "../../ReusableComponents/ErrorMessageComponent/MandatoryMsg";
import axios from "../../axios";
import {
  handleDisplayProperty,
  setMandatoryControls,
  updateValue,
} from "../../components/IFormDesignerLayout/IFormDesignerLogic";
import {
  GetState,
  GetStateFromData,
} from "../../components/CustomHooks/CustomHooks";
import ReactFroala from "./RichtextEditor/ReactFroala";
import { useDispatch} from "../../Redux";
import { updateFormStateContextWithoutRender } from "../../Redux/Actions/FormStateActions.js";


const RICHTEXTEDITOR = (props) => {
  const dispatch = useDispatch()
  const contexts = GetStateFromData(props.containerId);

  const { tempCanWidth, controlSelected, mandatoryControls } = GetState(
    "tempCanWidth",
    "controlSelected",
    "mandatoryControls"
  );
  const {
    IdPrecedence,
    modeReadOrWrite,
    openQuickToolFor,
    deleteControl,
    formState2,
    device,
  } = contexts.contexts;

  const [showMandatoryMsg, setShowMandatoryMsg] = useState(false);
  useEffect(() => {
    if (mandatoryControls.includes(props.data?.id)) {
      setShowMandatoryMsg(true);
    } else {
      setShowMandatoryMsg(false);
    }
  }, [mandatoryControls]);

  const data = props.data;
  const actualControlId = data.id;
  let mappingId = data["style"] && data["style"]["MappingId"];
  let userDefinedId = data["style"] && data["style"]["CustomId"];
  let userDefinedControlId = getSuggestedId(
    actualControlId,
    mappingId,
    userDefinedId,
    IdPrecedence
  );
  const [rerenderState, setReRenderState] = useState(false);
  const themeData = formState2.theme;

  const defaulRTStyles = {
    font_family: "Open Sans",
    font_size: "14px",
  };

  const defaultRTStyles = useRef(defaulRTStyles);

  const setRTStyles = (newStyle) => {
    const prevState = JSON.stringify(defaultRTStyles.current);
    defaultRTStyles.current = { ...defaultRTStyles.current, ...newStyle };
    if (!props.previewmode) {
      //check whether it's a designer mode or not
      //compare if some new changes has been made
      if (prevState !== JSON.stringify(defaultRTStyles.current)) {
        //rerender on new changes
        setReRenderState(!rerenderState);
      }
    }
  };

  useEffect(() => {
    if (themeData?.["BasicTheme"]?.["FontFamily"]) {
      setRTStyles({ font_family: themeData["BasicTheme"]["FontFamily"] });
    }
    if (themeData?.["BasicTheme"]?.["FontSize"]) {
      setRTStyles({ font_size: themeData["BasicTheme"]["FontSize"] });
    }
  }, [themeData?.["BasicTheme"]]);

  const basicThemeRTStyle = {
    FontFamily: defaultRTStyles.current.font_family,
    FontSize: defaultRTStyles.current.font_size,
  };

  const elementRefrence = useRef(null);
  const conditionalQuickToolOpening =
    props.id === openQuickToolFor ? true : false;

  var Editable = props.data.style.Enable;
  const refFlag8 = useRef(0);

  console.log("openQuickTool", openQuickToolFor);
  const refFlag7 = useRef(0);
  if (openQuickToolFor !== undefined && props.id === openQuickToolFor) {
    refFlag8.current = 1;
  }
  if (props.previewmode === undefined || props.previewmode === false) {
    refFlag7.current = 0;
  }
  if (props.previewmode && modeReadOrWrite === "r") {
    props.data.style.Enable = "false";
    Editable = "false";
    refFlag7.current = 1;
  }
  if (
    refFlag8.current === 1 &&
    props.data.style["Enable"] === "false" &&
    refFlag7.current === 0
  ) {
    Editable = "false";
    props.data.style.Enable = "false";
  }
  if (
    refFlag8.current === 1 &&
    props.data.style["Enable"] === "true" &&
    refFlag7.current === 0
  ) {
    Editable = "true";
    props.data.style.Enable = "true";
  }

  const isSubformInPage = props?.isSubformInPage || false;

  const [open, setOpen] = useState(conditionalQuickToolOpening);
  const [content, setContent] = useState(
    props.data && props.data.value && props.data.value.length > 0
      ? props.data.value
      : ""
  );

  const [showEditor, setShowEditor] = useState(false);

  const [mobileDisplay, setMobileDisplay] = useState(true);
  const [tabDisplay, setTabDisplay] = useState(true);

  const startMobile = useRef();
  const endMobile = useRef();
  const startTab = useRef();
  const endTab = useRef();

  useEffect(() => {
    if (formState2 && formState2.style) {
      startMobile.current = parseInt(
        formState2.style?.["Breakpoint"]?.["Mobile"]?.["StartValue"]
      );
      endMobile.current = parseInt(
        formState2.style?.["Breakpoint"]?.["Mobile"]?.["EndValue"]
      );
      startTab.current = parseInt(
        formState2.style?.["Breakpoint"]?.["Tab"]?.["StartValue"]
      );
      endTab.current = parseInt(
        formState2.style?.["Breakpoint"]?.["Tab"]?.["EndValue"]
      );
    }
  }, [
    formState2.style?.["Breakpoint"],
    formState2.style?.["Breakpoint"]?.["Mobile"],
    formState2.style?.["Breakpoint"]?.["Tab"],
  ]);

  useEffect(() => {
    if (props?.previewmode)
      handleDisplayProperty(
        props,
        device,
        setMobileDisplay,
        setTabDisplay,
        startMobile.current,
        endMobile.current,
        startTab.current,
        endTab.current
      );
  }, [
    props?.data?.style?.["MobileVisible"],
    props?.data?.style?.["TabsVisible"],
    device,
    endMobile,
    endTab,
    startMobile,
    startTab,
    tempCanWidth,
  ]);

  var jsonForLabel = {};
  var jsonForTextEditorControl = {};
  let backgroundColorDesigner ={
    backgroundColor: `${
      props?.data?.style?.["BackgroundColor"]
        ? props?.data?.style?.["BackgroundColor"]
        : "#f8f8f8"
    }`
  }
  let backgroundColorPreview = {
    backgroundColor:
    props.data.style.Enable === "false"
      ? "#F0F0F0"
      : `${
          props?.data?.style?.["BackgroundColor"]
            ? props?.data?.style?.["BackgroundColor"]
            : "#f8f8f8"
        }`
  }
  let borderStyle = {
    "border-radius":
      props.data.style["BorderRadius"],
    "border-width": props.data.style["BorderWidth"],
    "border-color":
      showMandatoryMsg && props.previewmode
        ? "red"
        : props.data.style["BorderColor"],
    "border-style": "solid"
  };
  if (props && props.data && props.data.style) {
    jsonForLabel["font-family"] =
      props.data.style["FontFamily"] || basicThemeRTStyle.FontFamily;
    jsonForLabel["font-size"] =
      props.data.style["FontSize"] && props.data.style["FontSize"] !== "Select"
        ? props.data.style["FontSize"]
        : basicThemeRTStyle?.FontSize;
    jsonForLabel["font-weight"] = props.data.style["FontWeight"];
    jsonForLabel["color"] = props.data.style["FontColor"];
    jsonForLabel["font-style"] = props.data.style["FontStyle"];

    jsonForLabel["line-height"] = props.data.style["FontSize"];

    if (
      document.getElementById(props.id + "richtext") &&
      document.getElementById(props.id + "richtext") !== null
    ) {
      document.getElementById(props.id + "richtext").style[
        "border-bottom-right-radius"
      ] = props.data.style["BorderRadius"];
      document.getElementById(props.id + "richtext").style[
        "border-bottom-left-radius"
      ] = props.data.style["BorderRadius"];
    }
    if (
      props.data.style["MaxHeight"] &&
      document.getElementsByClassName(
        "se-wrapper-inner se-wrapper-wysiwyg sun-editor-editable"
      ).length > 0 &&
      document.getElementsByClassName(
        "se-wrapper-inner se-wrapper-wysiwyg sun-editor-editable"
      )[0]
    ) {
      document.getElementsByClassName(
        "se-wrapper-inner se-wrapper-wysiwyg sun-editor-editable"
      )[0].style["max-height"] = props.data.style["MaxHeight"] + "px";
    }
  }

  var mandatoryElem = null;
  if (
    props.data &&
    props.data.style &&
    props.data.style["Mandatory"] !== undefined &&
    props.data.style["Mandatory"] !== null &&
    props.data.style["Mandatory"] === "true"
  ) {
    mandatoryElem = (
      <>
        <span style={{ flexGrow: 1, marginLeft: "5px", color: "#D53D3D" }}>
          {" "}
          *{" "}
        </span>
      </>
    );
  }

  function removeIdFromMandatoryControl(value) {
    if (mandatoryControls.includes(props.data.id)) {
      if (value.length > 0) {
        const newMandatoryList = mandatoryControls.filter(
          (item) => item !== props.data.id
        );
        setMandatoryControls([...newMandatoryList]);
      }
    }
  }

  function handleEditor2(event) {
    if(event==="richtextApi"){
      if(showEditor===false)
      setShowEditor(!showEditor);
    }
    if (event && event.target && event.target.getAttribute("divType")) {
      var divType = event.target.getAttribute("divType");
      if (
        divType === "richtexteditor" ||
        divType === "Label" ||
        divType === "openClose"
      ) {
        setShowEditor(!showEditor);
        var parentEle = event.target;
        var ispainted = parentEle.getAttribute("painted");
        if (divType === "Label" || divType === "openClose") {
          parentEle = parentEle.parentElement;
          ispainted = parentEle.getAttribute("painted");
          if (ispainted === undefined || ispainted === null) {
            parentEle = parentEle.parentElement;
            ispainted = parentEle.getAttribute("painted");
          }
        }
        if (ispainted === "false") {
          parentEle.setAttribute("painted", "true");       
          setContent("<p>test<p>");
        }
      }
    }
  } 
  dispatch(updateFormStateContextWithoutRender({"name":"handleEditor2","key":props?.containerId,"value":handleEditor2})) 

  var preparedStyle1 = CSSPreparation1(jsonForLabel);
  var styleSheet = StyleSheet.create({
    styling: preparedStyle1,
  });
  var preparedStyle2 = CSSPreparation1(jsonForTextEditorControl);
  var styleSheet2 = StyleSheet.create({
    styling: preparedStyle2,
  });

  function saveRTEData(jsonData) {
    return axios.post("/viewform/saveRichTextEditorData", jsonData, {
      withCredentials: true,
    });
  }

  function handleContent(content) {
    handleonBlurEvent(content);
    if (!window.isFormModified()) {
      window.setFormMoified(true);
    }
  }

  function handleonBlurEvent(contentData) {
    var jsonToSend = {
      controlId: props.data.id,
      richTextEditorJSON: [
        { id: props.data.id, data: contentData, IndexString: "" },
      ],
      gdtoken: contexts.gdtoken,
      witoken: contexts.witoken,
    };
    saveRTEData(jsonToSend).then((response) => {
      if (props.data) {
        props.data.value = response.data.message;
        updateValue(props.data.id, response.data.message, props.containerId);
        removeIdFromMandatoryControl(props.data.value);
      }
    });
  }

  var padding_left = props.data.style && props.data.style["SpacingLeft"];
  if (
    padding_left !== undefined &&
    padding_left !== null &&
    padding_left !== ""
  ) {
  } else {
    padding_left = "0px";
  }

  var padding_right = props.data.style && props.data.style["SpacingRight"];
  if (
    padding_right !== undefined &&
    padding_right !== null &&
    padding_right !== ""
  ) {
  } else {
    padding_right = "0px";
  }

  var padding_top = props.data.style && props.data.style["SpacingTop"];
  if (padding_top !== undefined && padding_top !== null && padding_top !== "") {
  } else {
    padding_top = "0px";
  }

  var padding_bottom = props.data.style && props.data.style["SpacingBottom"];
  if (
    padding_bottom !== undefined &&
    padding_bottom !== null &&
    padding_bottom !== ""
  ) {
  } else {
    padding_bottom = "0px";
  }

  var inputStylePadding = {
    position: "relative",
    display: "block",
  };
  var inputStylePadding1 = {
    position: "relative",
    display: "block",
  };

  if (props.previewmode) {
    if (
      props.data.style &&
      props.data.style["LabelValueAlignment"] &&
      props.data.style["LabelValueAlignment"] === "Left Right"
    ) {
      return (
        <div
          id={userDefinedControlId}
          style={{
            paddingLeft: padding_left,
            paddingRight: padding_right,
            paddingTop: padding_top,
            paddingBottom: padding_bottom,
          }}
        >
          {props.data &&
          props.data.style &&
          props.data.style["Visible"] === "true" ? (
            (props.data.style &&
              props.data.style["ReadOnlyStyle"] &&
              props.data.style["ReadOnlyStyle"] === "true") ||
            props.ReadOnlyStyle === "true" ? (
              <>
                <Form.Label
                  id={userDefinedControlId + "_label"}
                  style={{ marginLeft: "10px" }}
                  divType="Label"
                  className={css(styleSheet.styling)}
                  elem_type="textarea"
                  frameindex={data.frameIndex}
                  elementindex={data.elementIndex}
                >
                  {data.style && data.style.Label ? data.style.Label : ""}
                </Form.Label>
                <div style={{ display: "flex" }}>
                  <span>
                    {props.data.style &&
                      props.data.style.DummyData &&
                      props.data.style["DummyData"]}
                  </span>
                </div>
              </>
            ) : (
              <>
                <Form.Group
                  className={[
                    styles2.richtextEditorDiv,
                    "spacingThemeControls",
                    css(styleSheet2.styling),
                  ].join(" ")}
                  style={
                    mobileDisplay === false || tabDisplay === false
                      ? { display: "none" }
                      : { ...inputStylePadding }
                  }
                  divType="richtexteditor"
                  state={showEditor === true ? "expanded" : "collapsed"}
                  onClick={(event) => handleEditor2(event)}
                  control_id={userDefinedControlId + "_group"}
                  ref={elementRefrence}
                  key={data.id}
                  tabindex="1"
                  elem_type="textarea"
                  frameindex={data.frameIndex}
                  elementindex={data.elementIndex}
                >
                  <span style={{...borderStyle,...backgroundColorPreview}}>
                  <Row>
                    <Col>
                      <Form.Label
                        id={userDefinedControlId + "_label"}
                        style={{
                          wordBreak: "break-word",
                          marginLeft: "10px",
                          gap: "3px",
                          display: "flex",
                        }}
                        divType="Label"
                        className={css(styleSheet.styling)}
                        elem_type="textarea"
                        frameindex={data.frameIndex}
                        elementindex={data.elementIndex}
                      >
                        {data.style && data.style.Label ? data.style.Label : ""}

                        {mandatoryElem}

                        {(props.data.style && props.data.style["ToolTip"]) ||
                        props.data.style["ControlDescription"] ? (
                          <Tooltip tooltip={props.data.style.ToolTip} />
                        ) : (
                          <></>
                        )}
                      </Form.Label>
                    </Col>

                    <Col>
                      {showEditor === true ? (
                        <div
                          id={userDefinedControlId + "_arrowIcon"}
                          className={styles2.richtextEditorCollapsibleIcon}
                          divType="openClose"
                          state={showEditor === true ? "expanded" : "collapsed"}
                        >
                          <RichtextEditorDownArrow
                            divType="openClose"
                            state={
                              showEditor === true ? "expanded" : "collapsed"
                            }
                          />
                        </div>
                      ) : (
                        <div
                          id={userDefinedControlId + "_arrowIcon"}
                          className={styles2.richtextEditorCollapsibleIcon}
                          divType="openClose"
                          state={showEditor === true ? "expanded" : "collapsed"}
                        >
                          <RichtextEditorUpArrow
                            divType="openClose"
                            state={
                              showEditor === true ? "expanded" : "collapsed"
                            }
                          />
                        </div>
                      )}
                      {showEditor === true ? (
                        <div
                          style={{
                            padding: `${props.data.style?.["BorderRadius"]}`,
                          }}
                        >
                          <ReactFroala
                            data={props.data}
                            id={userDefinedControlId}
                            previewmode={props?.previewmode}
                            divType="editor"
                            handleContent={handleContent}
                            elem_type="textarea"
                            gdToken={contexts?.gdtoken}
                            wiToken={contexts?.witoken}
                          ></ReactFroala>
                        </div>
                      ) : null}
                    </Col>
                  </Row>
                  </span>
                </Form.Group>
                {showMandatoryMsg ? (
                  <MandatoryMsg
                    id={props.data.id}
                    mandatoryMsg={props.data?.style?.ValidationMessage}
                  />
                ) : null}
              </>
            )
          ) : null}
        </div>
      );
    } else {
      return (
        <div
          id={userDefinedControlId}
          style={{
            paddingLeft: padding_left,
            paddingRight: padding_right,
            paddingTop: padding_top,
            paddingBottom: padding_bottom,
          }}
        >
          {props.data &&
          props.data.style &&
          props.data.style["Visible"] === "true" ? (
            (props.data.style &&
              props.data.style["ReadOnlyStyle"] &&
              props.data.style["ReadOnlyStyle"] === "true") ||
            props.ReadOnlyStyle === "true" ? (
              <>
                <Form.Label
                  id={userDefinedControlId + "_label"}
                  style={{ marginLeft: "10px" }}
                  divType="Label"
                  className={css(styleSheet.styling)}
                  elem_type="textarea"
                  frameindex={data.frameIndex}
                  elementindex={data.elementIndex}
                >
                  {data.style && data.style.Label ? data.style.Label : ""}
                </Form.Label>
                <div style={{ display: "flex" }}>
                  <span>
                    {props.data.style &&
                      props.data.style.DummyData &&
                      props.data.style["DummyData"]}
                  </span>
                </div>
              </>
            ) : (
              <>
                <Form.Group
                  className={[
                    styles2.richtextEditorDiv,
                    "spacingThemeControls",
                    css(styleSheet2.styling),
                  ].join(" ")}
                  style={
                    mobileDisplay === false || tabDisplay === false
                      ? { display: "none" }
                      : { ...inputStylePadding}
                  }
                  divType="richtexteditor"
                  state={showEditor === true ? "expanded" : "collapsed"}
                  onClick={(event) => handleEditor2(event)}
                  control_id={userDefinedControlId + "_group"}
                  ref={elementRefrence}
                  key={data.id}
                  tabindex="1"
                  elem_type="textarea"
                  frameindex={data.frameIndex}
                  elementindex={data.elementIndex}
                >
                  <span style={{...borderStyle,display:"block",...backgroundColorPreview}}>
                  <span
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                      alignItems: "center"
                    }}
                    divType="Label"
                  >
                    <Form.Label
                      id={userDefinedControlId + "_label"}
                      style={{
                        wordBreak: "break-word",
                        marginLeft: "10px",
                        marginTop: "4pt",
                        gap: "3px",
                        display: "flex",
                        whiteSpace: "break-spaces",
                      }}
                      divType="Label"
                      className={css(styleSheet.styling) + " spacingThemeLabel"}
                      elem_type="textarea"
                      frameindex={data.frameIndex}
                      elementindex={data.elementIndex}
                    >
                      {data.style && data.style.Label ? data.style.Label : ""}

                      {mandatoryElem}

                      {(props.data.style && props.data.style["ToolTip"]) ||
                      props.data.style["ControlDescription"] ? (
                        <Tooltip tooltip={props.data.style.ToolTip} />
                      ) : (
                        <></>
                      )}
                    </Form.Label>

                    {showEditor === true ? (
                      <div
                        id={userDefinedControlId + "_arrowIcon"}
                        className={styles2.richtextEditorCollapsibleIcon}
                        divType="openClose"
                        state={showEditor === true ? "expanded" : "collapsed"}
                      >
                        <RichtextEditorDownArrow
                          state={showEditor === true ? "expanded" : "collapsed"}
                          divType="openClose"
                        />
                      </div>
                    ) : (
                      <div
                        id={userDefinedControlId + "_arrowIcon"}
                        className={styles2.richtextEditorCollapsibleIcon}
                        divType="openClose"
                        state={showEditor === true ? "expanded" : "collapsed"}
                      >
                        <RichtextEditorUpArrow
                          divType="openClose"
                          state={showEditor === true ? "expanded" : "collapsed"}
                        />
                      </div>
                    )}
                  </span>
                  {showEditor === true ? (
                    <div
                      style={{
                        padding: `${props.data.style?.["BorderRadius"]}`,
                      }}
                    >
                      <ReactFroala
                        previewmode={props.previewmode}
                        divType="editor"
                        data={props.data}
                        handleContent={handleContent}
                        gdToken={contexts?.gdtoken}
                        wiToken={contexts?.witoken}
                        id={userDefinedControlId}
                        elem_type="textarea"
                      />
                    </div>
                  ) : null}
                  </span>
                </Form.Group>
                
                {showMandatoryMsg ? (
                  <MandatoryMsg
                    id={props.data.id}
                    mandatoryMsg={props.data?.style?.ValidationMessage}
                  />
                ) : null}
              </>
            )
          ) : null}
        </div>
      );
    }
  } else {
    return (
      <div
        id={userDefinedControlId}
        style={{
          paddingLeft: padding_left,
          paddingRight: padding_right,
          paddingTop: padding_top,
          paddingBottom: padding_bottom,
        }}
      >
        <Form.Group
          className={[
            styles2.richtextEditorDiv,
            "spacingThemeControls",
            css(styleSheet2.styling),
          ].join(" ")}
          style={inputStylePadding1}
          onClick={
            isSubformInPage
              ? () => {}
              : (e) => props.handleSelected(e, props.id, "n")
          }
          divType="richtexteditor"
          state={showEditor === true ? "expanded" : "collapsed"}
          painted="false"
          onKeyDown={(e) => props.handleSelected(e)}
          control_id={data.id}
          key={data.id}
          tabindex="1"
          elem_type="textarea"
          frameindex={data.frameIndex}
          elementindex={data.elementIndex}
        >
          <span
            style={{...borderStyle , ...backgroundColorDesigner, display: "flex",
            justifyContent: "space-between",
            alignItems: "center"}}
          >
            <Form.Label
              control_id={data.id}
              id={userDefinedControlId + "_label"}
              style={{
                marginLeft: "10px",
                marginTop: "4pt",
                gap: "3px",
                display: "flex",
                wordBreak: "break-word",
                whiteSpace: "break-spaces",
              }}
              divType="Label"
              state={showEditor === true ? "expanded" : "collapsed"}
              className={css(styleSheet.styling) + " spacingThemeLabel"}
              elem_type="textarea"
              frameindex={data.frameIndex}
              elementindex={data.elementIndex}
            >
              {data.style && data.style.Label ? data.style.Label : ""}

              {mandatoryElem}

              {(props.data.style && props.data.style["ToolTip"]) ||
              props.data.style["ControlDescription"] ? (
                <Tooltip tooltip={props.data.style.ToolTip} />
              ) : (
                <></>
              )}
            </Form.Label>

            {showEditor === true ? (
              <div
                className={styles2.richtextEditorCollapsibleIcon}
                divType="openClose"
                state={showEditor === true ? "expanded" : "collapsed"}
              >
                <RichtextEditorDownArrow
                  divType="openClose"
                  state={showEditor === true ? "expanded" : "collapsed"}
                />
              </div>
            ) : (
              <div
                className={styles2.richtextEditorCollapsibleIcon}
                divType="openClose"
                state={showEditor === true ? "expanded" : "collapsed"}
              >
                <RichtextEditorUpArrow
                  divType="openClose"
                  state={showEditor === true ? "expanded" : "collapsed"}
                />
              </div>
            )}
          </span>
          {showEditor === true ? (
            <div style={{ padding: `${props.data.style?.["BorderRadius"]}` }}>
              <ReactFroala
                previewmode={props?.previewmode}
                divType="editor"
                data={props.data}
                id={userDefinedControlId}
                elem_type="textarea"
                gdToken={contexts?.gdtoken}
                wiToken={contexts?.witoken}
              ></ReactFroala>
            </div>
          ) : null}
          {controlSelected?.id === props?.id && (
            <ElemDeleteControl
              isSubformInPage={isSubformInPage}
              containerId={props.containerId}
              id={props.data.id}
              data={props.data}
              deleteControl={deleteControl}
              elemType={props.data?.elem_type}
              setDragging={props.setDragging}
              handleReorderStart={props.handleReorderStart}
              handleReorderDrop={props.handleReorderDrop}
              dragId={props.dragId}
              dragType={"column"}
            />
          )}
          {window?.openQuickTools && open && (
            <QuickTools
              containerId={props.containerId}
              selectedControl={props.data}
              open={open}
              setOpen={setOpen}
            />
          )}
        </Form.Group>
      </div>
    );
  }
};

export default RICHTEXTEDITOR;
